import React from 'react';
import styled from 'styled-components';
import DayScheduleList from './DayScheduleList';
import HorizontalScroll from 'react-scroll-horizontal';

const ScheduleWrapper = styled.div`
  padding-left: 100px;
  box-sizing: border-box;
  width: auto;
  height: 100%;
  display: flex;
  overflow: hidden;
  z-index: 10;
`;

const HackathonSchedule = () => {
  return (
    <ScheduleWrapper className="fade-in">
      <HorizontalScroll>
        <DayScheduleList program={Day1Program} day="DAY1" />
        <DayScheduleList program={Day2Program} day="DAY2" />
      </HorizontalScroll>
    </ScheduleWrapper>
  );
};

export default HackathonSchedule;
const Day1Program = [
  {
    time: '16:00 ~ 17:00',
    title: '',
    summary1: '  ',
    summary2: ' ',
  },

  {
    time: '21:00 ~ 21:30',
    title: ' 1',
    summary1: ' ~!',
    summary2: ' ',
  },
];
const Day2Program = [
  {
    time: '00:00 ~ 00:30',
    title: ' 2',
    summary1: '   ?',
    summary2: ' ',
  },
  {
    time: '03:00 ~ 00:30',
    title: ' 3',
    summary1: '   ?!',
    summary2: '',
  },
  {
    time: '11:30 ~ 12:00',
    title: '',
    summary1: '2022 GDSC KR',
    summary2: 'WINTER HACK ',
  },
  {
    time: '12:00 ~ 14:00',
    title: '',
    summary1: '2022 GDSC KR',
    summary2: 'WINTER HACK ',
  },
  {
    time: '14:00 ~ 15:00',
    title: ' ',
    summary1: '2022 GDSC KR',
    summary2: 'WINTER HACK  ?',
  },
];
